.home {
  width: 90%;
  height: 90%;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding-bottom: 100px;

  &__head {
    display: flex;
    align-items: center;

    .head_left {
      h2 {
        font-size: 40px;
        color: #a5a5a5;
        line-height: 1.2;
      }

      h1 {
        font-size: 60px;
        line-height: 1.2;
      }
    }

    .head_right {
    }
  }

  &__body {

    .body_left {
      display: flex;
      flex-direction: column;

      strong {
        font-size: 160px;
        line-height: 1;
        color: #5b8cff;
      }

      span {
        font-size: 25px;
      }
    }

    .body_right {
    }

  }

  &__footer {
    .footer-weather-card {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}